.host {
    padding-top: 30px;
    display: grid;
    grid-template-columns: 4fr 3fr;

    .hosting-header {
        width: 1250px;
        padding-top: 30px;

        :nth-child(2) {
            color: $clr2;
            text-decoration: underline;
            font-family: Circular-Light;
            font-size: rem(14px);
            font-weight: 600;
        }
    }

    .statistics {

        justify-self: end;
        margin-top: 50px;
        gap: 30px;

        >* {
            border: solid 1px $clr9;
            border-radius: 10px;
            width: rem(300px);
            height: rem(130px);
            box-shadow: rgb(0 0 0 / 12%) 0px 6px 16px;
            padding: 20px;
        }

        .statistics-icon {
            height: 50px;
            width: 50px;
        }

        h4 {
            font-size: rem(40px);
            font-weight: 400;
            padding-left: 10px;
        }

        h3 {
            color: $clr2;
            font-size: rem(17px);
            padding-left: 10px;
        }

        .orders,
        .income,
        .rate,
        .guests {

            :last-child {
                padding-top: 10px;
                align-items: center;
            }
        }

    }
}