.order-preview {

    td {
        white-space: nowrap;
    }

    td:nth-child(7) {
        font-weight: 600;
        cursor: pointer;

        &:hover {
            opacity: 0.8;
        }
    }

    .green {
        color: #009879;
    }

    .red {
        color: #e61e4d;
    }

    .black {
        color: $clr3;
    }
}