.details-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid $clr5;
    background-color: transparent;
    padding: 0 80px;
    height: 80px;
    position: block;
    width: 100%;
    color: $clr3;
    z-index: 20;
    top: 0;

    >* {
        flex: 1;
    }

    .main-nav-container {
        justify-content: flex-end;
        white-space: nowrap;


        .main-nav {
            margin-inline-end: 10px;

            a {
                align-items: center;
                border-radius: 25px;
                display: flex;
                height: 40px;
                padding: 15px;
                font-weight: 600;
                font-size: rem(14px);
            }

            a:hover {
                background-color: $clr6-opacity
            }
        }
    }

    .logo {
        color: $clr1;
        font-size: rem(30px);
        font-weight: bold;
        font-family: DancingScript-Regular;
        cursor: pointer;
    }

    .user-menu {
        align-items: center;
        display: flex;
        height: 40px;
        justify-content: space-between;
        padding-inline-start: 15px;
        position: relative;
        width: 80px;
        border-radius: 25px;
        border: 1px solid $clr6;
        background: $clr8;

        svg {
            stroke: black;
            height: 35px;
            width: 35px;
        }

        img {
            width: 16px;
            height: 16px;
            display: none;
        }
    }

    .curr-user-img {
        border-radius: 15px;
    }
}

